.semester-header {
  margin: 0px;
  display: flex;
  align-items: center;
  width: 100%;
}
.semester-header__select {
  margin: 0px;
  min-width: 164px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px;
  box-sizing: border-box;
  background: var(--main-white-color);
  border-radius: var(--border-radius);
}
.semester-header__select_status_open {
  border-radius: 8px 8px 0px 0px;
}
.semester-header__select:hover {
  cursor: pointer;
}
.semester-header__select-main {
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 8px 8px 20px;
  box-sizing: border-box;
}
.semester-header__select-text {
  margin: 0px 20px 0px 0px;
  font-style: normal;
  font-weight: 900;
  font-size: 16px;
  line-height: 20px;
  color: var(--main-black-color);
}
.semester-header__select-arrow {
  margin: 0px 0px 0px 10px;
  width: 24px;
  height: 24px;
  background-image: url('../../images/arrow.svg');
  background-position: center;
  background-size: 24px;
  background-repeat: no-repeat;
  transform: rotate(0deg);
  transition: all .3s ease-in-out;
}
.semester-header__select-arrow_status_open {
  transform: rotate(180deg);
}
.semester-header__select-options-container {
  width: 100%;
  margin: 0px;
  background: var(--main-white-color);
  border-radius: 0px 0px 8px 8px;
  display: none;
  position: absolute;
  top: 40px;
  left: 0px;
  transition: all .3s ease-in-out;
  z-index: 100;
  box-sizing: border-box;
  box-shadow: 0px 8px 12px -8px rgb(166 166 166 / 90%);
}
.semester-header__select-options-container_status_open {
  display: block;
}
.semester-header__select-options-list {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
}
.semester-header__select-options-item {
  margin: 0px 20px;
  padding: 11px 0px;
  box-sizing: border-box;
  border-bottom: 1px solid var(--slider-color);
}
.semester-header__select-options-item:last-of-type {
  border-bottom: none;
}
.semester-header__select-options-text {
  margin: 0px;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  color: var(--main-black-color);
}
.semester-header__select-options-item:hover .semester-header__select-options-text {
  color: var(--accent-blue-color);
}
.semester-header__btn-back {
  margin: 0px;
  min-width: 164px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 8px 8px 20px;
  background: var(--main-white-color);
  border-radius: var(--border-radius);
  border: none;
  transition: all .3s ease-in-out;
}
.semester-header__btn-back:hover {
  cursor: pointer;
}
.semester-header__btn-back-text {
  margin: 0px 20px 0px 0px;
  font-style: normal;
  font-weight: 900;
  font-size: 16px;
  line-height: 20px;
  color: var(--main-black-color);
}
.semester-header__btn-back-arrow {
  width: 24px;
  height: 24px;
  background-image: url('../../images/arrow.svg');
  background-position: center;
  background-size: 24px;
  background-repeat: no-repeat;
  transform: rotate(270deg);
}
.semester-header__control-list {
  margin: 0px 0px 0px auto;
  padding: 0px;
  list-style-type: none;
  display: flex;
  align-items: center;
}
.semester-header__control-item {
  margin: 0px 22px 0px 0px;
}
.semester-header__control-item:last-of-type {
  margin: 0px;
}
.semester-header__control-item-container {
  width: 40px;
  height: 40px;
  background: var(--main-white-color);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--border-radius);
}
.semester-header__control-item-container:hover {
  background: var(--gradient-blue-color);
  cursor: pointer;
}
.semester-header__control-btn {
  width: 24px;
  height: 24px;
  fill: var(--main-black-color);
}
.semester-header__control-item-container:hover .semester-header__control-btn {
  fill: var(--main-white-color);
}
